<template>
  <div class="prepaid">
    <h1 class="prepaid-title">
      <img src="@/assets/logo.gif" alt />
      <span>HPAY</span>
      <span class="subtitle">支付体验</span>
    </h1>
    <h2 class="prepaid-desc">个人付款对账系统</h2>
    <van-cell-group>
      <van-field
        v-model="user.payer_name"
        required
        clearable
        label="昵称"
        right-icon="question-o"
        placeholder="付款人昵称"
        :error-message="err_payer_name"
        @click-right-icon="$toast('可输入付款人昵称、微信号等')"
      />

      <van-field
        v-model="user.payer_email"
        type="email"
        required
        clearable
        label="邮箱"
        right-icon="question-o"
        placeholder="付款人邮箱"
        :error-message="err_payer_email"
        @click-right-icon="$toast('邮箱用于接收成功支付信息,QQ邮箱可能误判为垃圾邮件，请自行设置白名单')"
      />

      <van-field
        v-model="user.payer_mobile_number"
        clearable
        label="联系电话"
        type="tel"
        right-icon="question-o"
        placeholder="付款人联系电话"
        @touchstart.stop="show = true"
        @click-right-icon="$toast('联系电话用于接收成功支付信息')"
        :error-message="err_payer_mobile_number"
      />

      <van-field
        v-model="user.amount"
        clearable
        required
        label="交易金额"
        right-icon="question-o"
        placeholder="付款金额"
        :disabled="lockAmount"
        @focus="showActionSheet=!customAmount"
        :error-message="err_amount"
        @click-right-icon="amountTip"
      />

      <van-field
        v-model="user.payer_message"
        clearable
        label="给对方留言"
        type="textarea"
        right-icon="question-o"
        placeholder="请输入留言信息"
        @click-right-icon="$toast('留言信息将有助于与对方进一步的交流')"
      />
    </van-cell-group>

    <van-radio-group v-model="user.pay_type" class="paytype">
      <van-grid :border="false" :column-num="2">
        <van-grid-item>
          <van-radio name="微信" checked-color="#07c160">
            <div class="paytype-wx"></div>
          </van-radio>
        </van-grid-item>
        <van-grid-item>
          <van-radio name="支付宝" checked-color="#07c160">
            <div class="paytype-alipay"></div>
          </van-radio>
        </van-grid-item>
      </van-grid>
    </van-radio-group>

    <van-cell-group>
      <van-button
        :disabled="submitEnabled"
        type="primary"
        class="prepaid-btn"
        size="large"
        @click="submitPrePaid"
      >确认发起支付</van-button>
    </van-cell-group>

    <van-cell-group>
      <van-swipe :autoplay="3000">
        <van-swipe-item
          v-for="(image, index) in adImages"
          :key="index"
          class="prepaid-swipe-container"
        >
          <van-tag type="danger" class="prepaid-swipe-desc">广告</van-tag>
          <img v-lazy="image" class="swipe" />
        </van-swipe-item>
      </van-swipe>
    </van-cell-group>

    <van-action-sheet round overlay
       title="选择支付体验"
       v-model="showActionSheet" :actions="actions"
      @select="onSelect" />
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Prepaid",
  data() {
    return {
      showActionSheet: false,
      lockAmount: false,
      customAmount: false,
      amountTipStr: "请务必和实际支付的金额保持一致",
      actions: [
        { name: "自定义金额体验支付" },
        { name: "捐助1元开始体验支付" },
        { name: "捐助10元获取当前源码",subname:"自动发送" },
        { name: "捐助20元获取源码持续升级及解答" ,subname:"自动发送"}
      ],
      submitEnabled: false,
      user: {
        payer_name: "",
        payer_email: "",
        payer_mobile_number: "",
        amount: "",
        payer_message: "",
        pay_type: "微信"
      },
      err_payer_name: "",
      err_payer_email: "",
      err_payer_mobile_number: "",
      err_amount: "",
      adImages: [
        "https://img.yzcdn.cn/vant/apple-1.jpg",
        "https://img.yzcdn.cn/vant/apple-2.jpg"
      ]
    };
  },
  methods: {
     onSelect(item) {
      if(item.name.includes("1元")) this.user.amount = '1'
      if(item.name.includes("10")) this.user.amount = '20'
      if(item.name.includes("20")) this.user.amount = '50'
      if(item.name.includes("自定义")){
        this.customAmount = true
      } else {
        this.lockAmount = true
      }
      this.showActionSheet = false;
      this.amountTipStr = item.name
    },
    amountTip () {
      this.$toast(this.amountTipStr)
    },
    async submitPrePaid() {
      let _amount = this.customAmount ? 0 : this.user.amount
      let error_details = "";
      if (this.user.payer_name == "") {
        this.err_payer_name = "付款人昵称不能为空";
        error_details += this.err_payer_name + ";";
      }
      if (this.user.payer_email == "") {
        this.err_payer_email = "付款人邮箱不能为空";
        error_details += this.err_payer_email + ";";
      }
      if (this.user.payer_email != "") {
        let reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
        if (!reg.test(this.user.payer_email)) {
          this.err_payer_email = "请输入有效的邮箱地址";
          error_details += this.err_payer_email + ";";
        }
      }
      if (this.user.payer_mobile_number != "") {
        let reg = /^1[3456789]\d{9}$/
        if (!reg.test(this.user.payer_mobile_number)) {
          this.err_payer_mobile_number = "请输入有效的手机号码";
          error_details += this.err_payer_mobile_number + ";";
        }
      }
      if (this.user.amount == "") {
        this.err_amount = "请输入付款金额";
        error_details += this.err_amount + ";";
      } else {
        let reg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
          if (!reg.test(this.user.amount)) {
          this.err_amount = "输入金额字符非法";
          error_details += this.err_amount + ";";
        }
      }
      if (this.user.amount < 1) {
        this.err_amount = "为防止恶意请求测试金额必须大于1元";
        error_details += this.err_amount + ";";
      }
      if (error_details != "") {
        return;
      }
      this.submitEnabled = true;
      let { data, code } = (await axios.post(
        "http://xhl.liuchangxi.com/api/pay/pending",
        this.user
      )).data;
      if (code == 200) {
        this.$router.push({
          name: "scan",
          params: {
            pay_type: this.user.pay_type,
            id: data.id,
            id_code: data.id_code,
            amount: _amount
          }
        });
      } 
    }
  }
};
</script>

